<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>userid: {{userID}}</p>
    <p>username: {{userName}}</p>
    <button @click="enableMic()">{{ microphoneState? '关': '开'}}麦克风</button>
    <button @click="enableSpeaker()">{{ speakerState? '关': '开'}}扬声器</button>
    <button @click="enableCamera()">{{ cameraState? '关': '开'}}摄像头</button>
    <hr>
    <video controls autoplay></video>
  </div>
</template>

<script>
import { ZegoExpressEngine } from 'zego-express-engine-webrtc';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      zg: null,
      rangeAudio: null,
      speakerState: true,
      microphoneState: false,
      cameraState: false, 
      localStream: null,
      appID: 1739272706,
      server: 'wss://webliveroom-test.zego.im/ws',
      userID: 'user_' + new Date().getTime(), // userID 用户自己设置，必须保证全局唯一
      userName: '阿拉贡',// userName 用户自己设置，没有唯一性要求
      roomID: "room01", // roomID 用户自己设置，必须保证全局唯一
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.zg = new ZegoExpressEngine(this.appID, this.server);
      window.ra = this.rangeAudio = this.zg.createRangeAudioInstance();
      window.zg = this.zg;
      // 加入房间
      fetch(`https://wsliveroom-alpha.zego.im:8282/token?app_id=${this.appID}&id_name=${this.userID}`)
          .then(rsp=> rsp.text())
          .then(token=> {
              this.zg.loginRoom(this.roomID, token, { 
                userID: this.userID, userName: this.userName 
              }, { userUpdate: true })
          });

      this.zg.on('roomStateUpdate', async (roomID, state, errorCode, extendedData) => {
        console.log('room state update', roomID, state, errorCode, extendedData)
          if(!errorCode) {
              if (state == 'CONNECTED') {// 加入房间，初始化设置
                const position = [0,0,0];
                const axisForward = [1,0,0];
                const axisRight = [0,1,0];
                const axisUp = [0,0,1];
                this.rangeAudio.updateSelfPosition(position, axisForward, axisRight, axisUp); 
                this.rangeAudio.setAudioReceiveRange(100);//设置音频接收距离的最大范围
                this.rangeAudio.enableSpatializer(false); // 3d 为 true 表示开启，为 false 表示关闭。默认是关闭。
                this.rangeAudio.enableMicrophone(this.microphoneState);// 麦克风 为 true 表示开启，为 false 表示关闭。
                this.rangeAudio.enableSpeaker(this.speakerState);// enable 为 true 表示开启，为 false 表示关闭
                // 参数 mode 可传 0 或 1。
                // 0 为世界模式，可与登录房间内的所有人交流。
                // 1 为仅小队模式，只与小队内的队员交流。
                this.rangeAudio.setRangeAudioMode(0);
              }
          }
      });
      
      this.rangeAudio.on("microphoneStateUpdate", (state, errorCode, extendedData) => {
        console.log('[microphoneStateUpdate]', state, errorCode, extendedData);
        if (state === 0) {
          // 关闭麦克风声音
        } else if(state === 1) {
          // 开启麦克风中
        } else if(state === 2) {
          // 已打开麦克风发送声音
        }
      })
      this.zg.on('roomUserUpdate', (roomID, updateType, userList)=> {
        console.log('[roomUserUpdate]',roomID, updateType, userList);
        // 更新房间里音源位置，此位置应通过信令方式下发，此处简答将所有人设置在原点
        if(updateType == 'ADD') {
          const position = [0,0,0];
          userList.forEach(item=> {
            this.rangeAudio.updateAudioSource(item.userID, position);
          });
        }
      });
      this.zg.on('microphoneStateUpdate', (state, errorCode, extendedData)=> {
        console.error('[microphoneStateUpdate]', state, errorCode, extendedData);
      });
      this.zg.setSoundLevelDelegate(true, 1000, {
        enableInBackground: false,
      });
      this.zg.on('soundLevelUpdate', (soundLevelList)=> {
        console.warn('[soundLevelUpdate]', soundLevelList);
      });
      this.zg.on('capturedSoundLevelUpdate', (soundLevel) => {
        console.warn('[capturedSoundLevelUpdate]', soundLevel);
      });
    },
    enableMic() {
      this.microphoneState = !this.microphoneState;
      this.rangeAudio.enableMicrophone(this.microphoneState);
    },
    enableSpeaker() {
      this.speakerState = !this.speakerState;
      this.rangeAudio.enableSpeaker(this.speakerState);
    },
    async enableCamera() {
      if(this.localStream) {
        this.zg.destroyStream(this.localStream);
        this.zg.stopPublishingStream('aaaa');
        this.localStream = null;
        return;
      }
      this.localStream = await this.zg.createStream({
        camera: {
          video: true, 
          audio: false,
          // videoQuality: 1,
          // videoInput:null
          }
        });
      this.zg.startPublishingStream('aaaa', this.localStream);
      document.querySelector('video').srcObject = this.localStream;
    }
  }
}
</script>

